import { format } from "timeago.js";

export default function (value, slice) {
  const time = format(value);
  let timeFormat = time.replace("seconds", "giy");
  timeFormat = timeFormat.replace("minutes", "pht");
  timeFormat = timeFormat.replace("hours", "gi");
  timeFormat = timeFormat.replace("days", "ngy");
  const idx = timeFormat.indexOf(" ago");
  return slice ? timeFormat.slice(0, idx) : timeFormat;
}
